import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";

import {
  ShowModalGudang,
  showModal,
  getDataGudang,
  hideModal,
  } from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifSucces,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import { reset } from "redux-form";
import { deleteData, postData, putData } from "../../../config/axios.jsx";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import FormModalMasterGudang from "./FormModalMasterGudang.jsx";

const maptostate = (state) => {
  return {
    DataGudang: state.datamaster.getDataGudang,
    hideModal: state.datamaster.modalDialog,
  };
};

const hapusDataGudang = (kode,params, dispatch) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>Kode Gudang " +
      kode +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      deleteData("gudang/delete/by-kode-gudang/" + params).then(() => {
        NotifSucces("Data Berhasil Di Hapus").then(() =>
          dispatch(getDataGudang())
        );
      }).catch((err) => {
        ToastNotification("error", err.response.data)
      })
    }
  });
};
class MasterGudang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      columns: [
        {
          dataField: "kode_gudang",
          text: "Kode Gudang ",
          sort: true,
        },
        {
          dataField: "nama_gudang",
          text: "Nama Gudang ",
          sort: true,
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              id: row._id,
              kode_gudang: row.kode_gudang,
              nama_gudang: row.nama_gudang,
            };
            return (
              <div className="row text-center">
                {row.kode_gudang === "TOKO" ? null : <>
                <div className="col-12">
                  <button
                    onClick={() => this.formEditDataKategori(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      hapusDataGudang(row.kode_gudang,row._id, this.props.dispatch)
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                  </div>
                  </>
                }
              </div>
            );
          },
        },
      ],
      // dataGudang: [
      //   {
      //     kode_gudang: "tk",
      //     nama_gudang: "Toko",
      //   },
      // ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(getDataGudang("master"));
  }
  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    // console.log(data);
    let data_edit = {
      kode_gudang: data.kode_gudang,
      nama_gudang: data.nama_gudang,
    };
    let data_post = {
      kode_gudang: data.kode_gudang,
      nama_gudang: data.nama_gudang,
    };
    this.state.isEdit
      ? putData("gudang/edit/by-kode-gudang/" + data.id, data_edit)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Diedit");
          })
          .then(() => {
            this.props.dispatch(getDataGudang());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("ModalMasterGudang"));
          })
        .catch((error) => {
          ToastNotification("info", error.response === undefined ? "Terjadi Kesalahan Saat Megirim Data " : error.response.data)
          this.setState({ isLoading: false });
          })
      : postData("gudang/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Disimpan");
          })
          .then(() => {
            this.props.dispatch(getDataGudang());
          })
          .then(() => {
            this.props.dispatch(reset("ModalMasterGudang"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) =>
            ErrorHandling(
              error,
              "gudang/reactive/by-kode-gudang/",
              // data.kode_gudang,
              data_post,
              this.props.dispatch,
              getDataGudang(),
              hideModal(),
              "ModalMasterGudang"
            ).then(() => {
              this.setState({ isLoading: false });
            })
          );
  }

  formEditDataKategori(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalGudang(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  shoModalGudang() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalGudang(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Gudang</li>
        </ol>
        <h1 className="page-header">Master Gudang </h1>
        <Panel>
          <PanelHeader>Master Gudang</PanelHeader>
          <br />

          {/* Master Gudang */}
          <div className="container">
            <Tabel
              keyField="kode_gudang"
              tambahData={true}
              handleClick={() => this.shoModalGudang()}
              exportCsv={true}
              data={this.props.DataGudang}
              columns={this.state.columns}
              empty={this.props.DataGudang}
              textEmpty="Data Gudang Kosong"
            />
          </div>
          <br />
          {/* End Master Gudang */}
          <ModalGlobal
            size="P"
            title={
              this.state.isEdit ? "Edit Data Gudang" : "Tambah Data Gudang"
            }
            content={
              <FormModalMasterGudang
                isEdit={this.state.isEdit}
                isLoading={this.state.isLoading}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* End Tambah Master Gudang  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterGudang);
